/*-------------- reset.css --------------------*/
html,body,div,ul,ol,li,h1,h2,h3,h4,h5,h6,form,input,lable,textarea,p,img{
    margin: 0;
    padding: 0; 
    /* -webkit-user-select: none; */
  /* -moz-user-select: none;  */
}
html,body{
    height:100%;
    background:#f5f5f5;
}
img {
    border: none;
    float: none;
}
fieldset {
    border: 0;
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
}
select,
input {
    vertical-align: middle;
    padding: 1px 0;
}

label {
    cursor: hand;
}
abbr,
acronym {
    border: 0;
}
button {
    margin:0;
    padding:0;
    border:none;
    outline: 0;
    background:#fff;
}
/*去除边框虚线*/

iframe {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
}

input {
    margin: 0;
    padding: 0;
    outline: none;
    border: 0;
}


/* 通用 */

::-webkit-input-placeholder {
    color: #999;
    font-size: 0.75rem;
}

::-moz-placeholder {
    color: #999;
    font-size: 0.75rem;
}


/* firefox 19+ */

:-ms-input-placeholder {
    color: #999;
    font-size: 0.75rem;
}


/* ie */

input:-moz-placeholder {
    color: #999;
    font-size: 0.75rem;
}


/*-------------- base.css --------------------*/

.fl {
    float: left;
}

.fr {
    float: right;
}

.hide {
    display: none;
}

.clear {
    clear: both;
    height: 0;
    overflow: hidden;
}


/*清除浮动*/

.clearfix {
    *zoom: 1;
}

.clearfix:after {
    display: block;
    content: "clear";
    height: 0;
    clear: both;
    overflow: hidden;
    visibility: hidden;
}


/*单行文字溢出虚点显示*/

.ell {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
